.search-box {
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid #ccc;

  .search {
    flex: 1;
    outline: none !important;
    box-shadow: none !important;
    border: 0;
    border-radius: 0;
    font-size: 11px;
  }

  .search-opt {
    flex-basis: 25px;
    display: flex;
    align-items: center;
    padding: 0;
    box-shadow: none !important;
    border-radius: 0;
    min-height: 25px;
    height: 25px;
    margin: 0;
    justify-content: center;
    cursor: pointer;
    border: 0;
    outline: none !important;
    border-left: 1px solid #ccc;
  }
}

.cactiva-tree {
  display: flex;
  flex-direction: column;
  flex: 1;
  .list {
    flex: 1;
    overflow-y: auto;
    position: relative;
    &.hover {
      background: #ececeb;
    }
    &.loading {
      opacity: 0.4;
    }
    .list-body {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
    }
    .item {
      cursor: default;
      user-select: none;
      display: flex;
      flex-direction: row;
      .icon {
        &.file {
          margin-top: 2px;
        }
        flex-basis: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      span {
        flex: 1;
        font-size: 11px;
      }
      &:hover,
      &.hover {
        background: #ececeb;
      }
      &.selected {
        background: #e3f2ff;
      }
    }
  }
}

.ctree-menu {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  span {
    color: #666 !important;
  }
}
